ACnB,uBAAuB,EAAE,yDAAyD;AACtF;AACA;IACI,UAAU;IACV,oBAAoB;AACxB;AACA;IACI,UAAU;AACd;AACA;IACI,UAAU;AACd;AACA;IACI,UAAU;AACd;AACA;IACI,UAAU;AACd;AACA;IACI,UAAU;AACd;AACA;IACI,UAAU;IACV,sBAAsB;IACtB,kBAAkB;IAClB,kBAAkB;;IAElB,uCAAuC;IACvC,eAAe;;IAEf,qBAAqB;IACrB,mBAAmB;;IAEnB,uCAAuC;IACvC;gEAC4D;;IAE5D,8CAA8C;IAC9C,gBAAgB;;IAEhB,2BAA2B;IAC3B,gBAAgB;;IAEhB,oCAAoC;IACpC,kCAAkC;;IAElC,kCAAkC;IAClC,cAAc;;IAEd,uBAAuB;IACvB,6DAA6D;IAC7D,0BAA0B;;IAE1B,sEAAsE;AAC1E;AACA;IACI,mBAAmB;IACnB,oBAAoB;AACxB;AACA;IACI,kBAAkB;AACtB;AACA;IACI,qBAAqB;AACzB;AACA;IACI,oBAAoB;AACxB;AACA;IACI,mBAAmB;AACvB;AACA;IACI,oBAAoB;AACxB;AACA;;EAEE;AACF;IACI,kBAAkB;AACtB;AACA;IACI,eAAe;IACf,kBAAkB;AACtB;AACA;IACI,kBAAkB;IAClB,QAAQ;IACR,cAAc;IACd,iBAAiB;IACjB,eAAe;IACf,cAAc;IACd,qBAAqB;IACrB,UAAU;IACV,aAAa;AACjB;AACA;IACI,MAAM;IACN,YAAY;IACZ,gBAAgB;AACpB","sourcesContent":["/*!\n * FilePond 4.31.1\n * Licensed under MIT, https://opensource.org/licenses/MIT/\n * Please visit https://pqina.nl/filepond/ for details.\n */\n\n/* eslint-disable */\n.filepond--assistant {\n    position: absolute;\n    overflow: hidden;\n    height: 1px;\n    width: 1px;\n    padding: 0;\n    border: 0;\n    clip: rect(1px, 1px, 1px, 1px);\n    -webkit-clip-path: inset(50%);\n    clip-path: inset(50%);\n    white-space: nowrap;\n}\n/* Hard to override styles */\n.filepond--browser.filepond--browser {\n    /* is positioned absolute so it is focusable for form validation errors */\n    position: absolute;\n    margin: 0;\n    padding: 0;\n\n    /* is positioned ~behind drop label */\n    left: 1em;\n    top: 1.75em;\n    width: calc(100% - 2em);\n\n    /* hide visually */\n    opacity: 0;\n    font-size: 0; /* removes text cursor in Internet Explorer 11 */\n}\n.filepond--data {\n    position: absolute;\n    width: 0;\n    height: 0;\n    padding: 0;\n    margin: 0;\n    border: none;\n    visibility: hidden;\n    pointer-events: none;\n    contain: strict;\n}\n.filepond--drip {\n    position: absolute;\n    top: 0;\n    left: 0;\n    right: 0;\n    bottom: 0;\n    overflow: hidden;\n    opacity: 0.1;\n\n    /* can't interact with this element */\n    pointer-events: none;\n\n    /* inherit border radius from parent (needed for drip-blob cut of) */\n    border-radius: 0.5em;\n\n    /* this seems to prevent Chrome from redrawing this layer constantly */\n    background: rgba(0, 0, 0, 0.01);\n}\n.filepond--drip-blob {\n    position: absolute;\n    -webkit-transform-origin: center center;\n    transform-origin: center center;\n    top: 0;\n    left: 0;\n    width: 8em;\n    height: 8em;\n    margin-left: -4em;\n    margin-top: -4em;\n    background: #292625;\n    border-radius: 50%;\n\n    /* will be animated */\n    will-change: transform, opacity;\n}\n.filepond--drop-label {\n    position: absolute;\n    left: 0;\n    right: 0;\n    top: 0;\n    margin: 0;\n    color: #4f4f4f;\n\n    /* center contents */\n    display: flex;\n    justify-content: center;\n    align-items: center;\n\n    /* fixes IE11 centering problems (is overruled by label min-height) */\n    height: 0px;\n\n    /* dont allow selection */\n    -webkit-user-select: none;\n    -moz-user-select: none;\n    -ms-user-select: none;\n    user-select: none;\n\n    /* will be animated */\n    will-change: transform, opacity;\n}\n/* Hard to override styles on purpose */\n.filepond--drop-label.filepond--drop-label label {\n    display: block;\n    margin: 0;\n    padding: 0.5em; /* use padding instead of margin so click area is not impacted */\n}\n.filepond--drop-label label {\n    cursor: default;\n    font-size: 0.875em;\n    font-weight: normal;\n    text-align: center;\n    line-height: 1.5;\n}\n.filepond--label-action {\n    text-decoration: underline;\n    -webkit-text-decoration-skip: ink;\n    text-decoration-skip-ink: auto;\n    -webkit-text-decoration-color: #a7a4a4;\n    text-decoration-color: #a7a4a4;\n    cursor: pointer;\n}\n.filepond--root[data-disabled] .filepond--drop-label label {\n    opacity: 0.5;\n}\n/* Hard to o